body {
  font-family: 'Segoe UI', Arial, sans-serif;
  margin: 0;
  padding: 0;
  background: #f5f7fa;
  color: #222;
}

header {
  background: #1a237e;
  color: #fff;
  padding: 30px 20px 10px 20px;
  text-align: center;
}

header h1 {
  margin: 0;
  font-size: 2.5em;
}

header .contact {
  margin-top: 10px;
  font-size: 1em;
}

header a {
  color: #ffeb3b;
  text-decoration: none;
}

main {
  max-width: 800px;
  margin: 30px auto;
  padding: 20px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
}

section {
  margin-bottom: 32px;
}

h2 {
  color: #1a237e;
  border-bottom: 2px solid #e3e3e3;
  padding-bottom: 4px;
  margin-top: 0;
}

ul {
  margin: 10px 0 0 20px;
}

.job h3 {
  margin-bottom: 2px;
}

footer {
  text-align: center;
  padding: 16px 0;
  background: #e3e3e3;
  color: #444;
  margin-top: 32px;
  font-size: 0.95em;
}
